import Vue from "vue";
// 导入自己需要的组件
import {
    Notification,
    Avatar,
    MenuItem,
    MenuItemGroup,
    Link,
    Switch,
    Menu,
    Cascader,
    Progress,
    Button,
    Carousel,
    Dialog,
    Row,
    Col,
    Drawer,
    Submenu,
    Image,
    Card,
    CarouselItem,
    Option,
    Select,
    Tabs,
    TabPane,
} from "element-ui";
const needArray = [
    Avatar,
    MenuItem,
    MenuItemGroup,
    Link,
    Switch,
    Menu,
    Cascader,
    Progress,
    Button,
    Carousel,
    Dialog,
    Row,
    Col,
    Drawer,
    Submenu,
    Image,
    Card,
    CarouselItem,
    Option,
    Select,
    Tabs,
    TabPane,
];
const element = {
    install: function(Vue) {
        needArray.forEach((i) => Vue.use(i));
    },
};
Vue.prototype.$notify = Notification;
export default element;
